@charset "UTF-8";

/* Set defaults for the whole page. */
/* NOTE: box-sizing important as this includes both width and
 * padding for elements. For more info see: https://www.w3schools.com/css/css3_box-sizing.asp
 */

body {
  height: 100%;
  width: 100%;
}

body,
html,
html * {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-tap-highlight-color: transparent; /* Required for Android mobile browser. Without this on a touch event the default background colur is cyan/light blue. */
  /*position:fixed;*/
}

input,
select,
textarea {
  font-size: 16px;
}

/*html {
  background:red;
  scroll-behavior: hidden;
}
*/

