.sidenav {
  height: 100%; /* 100%; */
  width: 0;
  position: fixed;
  -webkit-transform: none !important; /* Fix for Chrome 'position:fixed' */
  z-index: 5000;
  top: 0;
  right: 0;
  background-color: white; /* #c8c8c8;*/
  overflow-x: hidden;
  transition: 0.3s;
  overflow: auto;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  /*left: 0;
    bottom: 0;
    padding-top: 60px;*/
  /*border-bottom: 1px solid gray;*/
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 20px;
  color: black;
  display: block;
  transition: 0s;
}

.sidenav span {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 20px;
  color: black;
  display: block;
  transition: 0s;
}

/*.sidenav a:hover {
