}

/* 768 */
@media screen and (min-width: 768px) {
  .menu {
    display: none;
  }
  .sidenav a {
    display: flex;
    font-size: 20px;
  }
  .span {
    display: flex;
    font-size: 20px;
  }
  .sidenav {
    padding-top: 0;
    width: 100%;
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    position: relative;
    height: 60px;
  }
  .sidenav .closebtn {
    display: none;
  }

  .backgroundDimVisibility {
    display: none;
  }

  .noscroll {
    overflow: auto;
  }

  .headerTitle {
    width: 100%;
  }

  .headerBack {
    /*display:none;*/
  }

  navmenu navitemheader {
    display: none;
  }

  navmenu .navitemdisplaytoggle {
    display: none; /* display:block; */
  }

  navmenu navitem {
    padding-right: 20px;
    padding-top: 0px;
    padding-bottom: 5px;
  }

  ul,
  li {
    display: flex;
    flex-direction: row;
    padding: 10px;
    align-items: center;
  }

  navmenu navseparator,
  .hlMenu {
    display: none;
    /*margin-left: 10px;*/
  }

  .vlMenu, .vlMenuPadding {
    display: inline-block;
    height: 18px;
    margin-left: 10px;
  }
  .vlMenu {
    border-left: 1px solid gray;
    margin-left: 20px;
  }

  navmenu .vl {
    display: block;
  }

  header .vl {
    display: none;
  }

  .headerMenu {
    display: none;
  }

  .headerLogout {
    min-width: 100px;
    margin-right: 30px;
    font-size: 14px;
    font-weight: normal;
  }

  .sectionContainer {
    min-height: calc(100vh - 130px); /* calc(100vh - 130px); */
  }

  .sectionContainerHeader {
    min-height: 50px;
    font-size: 22px; /* 24px; */
  }

  .sectionContainerFixedHeader {
    border-top: 1px solid lightgray;
    margin-top: 106px; /* 86px */
  }

  .sectionHeadingBody {
    max-width: 80vw;
  }

  .navFixedHeader {
    position: fixed;
    -webkit-transform: none !important; /* Fix for Chrome 'position:fixed' */
    top: 46px;
    width: 100%;
    z-index: 5000;
  }

  .sidenavDisplayMenu {
    width: 100%;
  }
  .sidenavHideMenu {
    width: 100%;
  }

  navmenu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 20px;
  }

  navmenu navgroup {
    display: flex;
    margin-left: 10px;
    margin-right: 10px;
    align-items: center;
    justify-content: center;
    -webkit-padding-start: 0px;
    padding-left: 0px;
  }

  navmenu navitem {
  }

  navmenu navitemheader {
  }

  navmenu navgroup navitem {
    border-right: 1px solid darkgray;
    padding: 5px;
    font-size: 20px;
  }

  .navitemselect {
    background: white;
    border-bottom: 2px solid darkgray; /* !important; */
  }

  .bodyNoScroll {
    position: unset;
  }
}

